summary {
  padding: 10px 0px 10px 20px;
  background-color: hsl(207, 62%, 56%);
  cursor: pointer;
  color: white;
}
summary:hover {
  color: gold;
  background-color: hsl(217, 57%, 15%);
}
details[open] > summary {
  color: gold;
  background-color: hsl(217, 57%, 15%);
}

.personInformation {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.infoBlock {
  padding: 10px;
  border: 1px solid hsl(207, 62%, 56%);
  border-radius: 15px;
  margin: 10px 20px;
  color: white;
  background-color: hsl(207, 62%, 56%);
}
.infoBlock p {
  font-size: 18px;
}
.statements {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
@media screen and (max-width: 1100px) {
  .statements {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 710px) {
  .statements {
    grid-template-columns: repeat(1, 1fr);
  }
}
.statementsClear {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
}
.statementsClear h3 {
  color: black;
  font-size: 35px;
  border-bottom: 1px solid black;
}

.line {
  height: 40px;
  background-color: hsl(217, 57%, 15%);
}
.sendStatement {
  width: 300px;
  height: 100px;
  border-radius: 15px;
  background-color: hsl(207, 62%, 56%);
  color: white;
  font-size: 25px;
  border: none;
  cursor: pointer;
}

.sendStatement:hover {
  background-color: hsl(217, 57%, 15%);
  color: gold;
}
